/* --------------------------------------------------
	Number Counters
-------------------------------------------------- */

function initCountNbr () {
	var hasCounters = $('#counters').hasClass('count-wrapper');
	if (hasCounters) {
		var options = {
			separator : ',',
			useEasing : true,
			useGrouping : true, 
		};
		// Counter 1
		counter1 = new CountUp('count-1', 0, 8, 0, 4, options);
		// Counter 2
		counter2 = new CountUp('count-2', 0, 30, 0, 4, options);
		// Counter 3
		counter3 = new CountUp('count-3', 0, 40, 0, 4, options);
		// Counter 4
		counter4 = new CountUp('count-4', 0, 1500, 0, 4, options);
	}

	/* ---- Hover Counters --------------------- */
	$('.number-counters-simple .count-wrapper .count-item:nth-child(1)').hover(function () { counter1.reset(); counter1.start(); }, function () { /* Reset */ });
	$('.number-counters-simple .count-wrapper .count-item:nth-child(2)').hover(function () { counter2.reset(); counter2.start(); }, function () { /* Reset */ });
	$('.number-counters-simple .count-wrapper .count-item:nth-child(3)').hover(function () { counter3.reset(); counter3.start(); }, function () { /* Reset */ });
	$('.number-counters-simple .count-wrapper .count-item:nth-child(4)').hover(function () { counter4.reset(); counter4.start(); }, function () { /* Reset */ });
} // initCountNbr

/* --------------------------------------------------
	initFloatingLayersAnimation
-------------------------------------------------- */

function initFloatingLayersAnimation() {
	const layer_1 = document.querySelector('.float-Y-1');
	layer_1.addEventListener('animationend', () => {
		$('.float-Y-1.is-animated').removeClass("is-animated");
		$('.float-Y-2').addClass("is-animated");
		// console.log('Layer-1 Animation End');
	});
	const layer_2 = document.querySelector('.float-Y-2');
	layer_2.addEventListener('animationend', () => {
		$('.float-Y-2.is-animated').removeClass("is-animated");
		$('.float-Y-3').addClass("is-animated");
		// console.log('Layer-2 Animation End');
	});
	const layer_3 = document.querySelector('.float-Y-3');
	layer_3.addEventListener('animationend', () => {
		$('.float-Y-3.is-animated').removeClass("is-animated");
		$('.float-Y-1').addClass("is-animated");
	});
}

/* --------------------------------------------------
	initCircleProgress
-------------------------------------------------- */

function initCircleProgress() {
	$('.cs-layers-img .layer-1 .layer-circle').circleProgress({
		value: 0.9,
		size: 1000,
		fill: { gradient: [["#FFD100", .5], ["#00CDFF", .5]], gradientAngle: Math.PI / 4 },
	});
}

/* --------------------------------------------------
	initLayerImagesHover
-------------------------------------------------- */

function initLayerImagesHover() {
	/* ---- Hover Hero Layer 1 --------------------- */
	$('#codescrum-hero .overlay .cs-layers-img .layer.layer-1').hover(function () {
		$('#codescrum-hero .overlay .cs-layers-img .layer.layer-2 .layer-content').css({ 'opacity': '0.2', 'filter': 'grayscale(100%)' });
		$('#codescrum-hero .overlay .cs-layers-img .layer.layer-3 .layer-content').css({ 'opacity': '0.2', 'filter': 'grayscale(100%)' });
		// Init Barchart animation
		$('#codescrum-hero .overlay .cs-layers-img .layer.layer-1 .layer-content').addClass("no-width is-animated");
		// Animate CircleProgress
		$('.cs-layers-img .layer-1 .layer-circle').circleProgress({
			value: 0.9,
			size: 1000,
			fill: { gradient: [["#FFD100", .5], ["#00CDFF", .5]], gradientAngle: Math.PI / 4 },
		}).on('circle-animation-progress', function (event, progress) {
			$(this).find('strong').html(Math.round(100 * progress) + '<i>%</i>');
			fill: {
				gradient: ['#fffff', '#000000'];
			}
		});
	}, function () {
		// Reset the Styles
		// Init Barchart animation
		$('#codescrum-hero .overlay .cs-layers-img .layer.layer-1 .layer-content').removeClass("no-width is-animated");
		$('#codescrum-hero .overlay .cs-layers-img .layer.layer-2 .layer-content').css({ 'opacity': '1', 'filter': 'grayscale(0%)' });
		$('#codescrum-hero .overlay .cs-layers-img .layer.layer-3 .layer-content').css({ 'opacity': '1', 'filter': 'grayscale(0%)' });
	});
	/* ---- Hover Hero Layer 2 --------------------- */
	$('#codescrum-hero .overlay .cs-layers-img .layer.layer-2').hover(function () {
		$('#codescrum-hero .overlay .cs-layers-img .layer.layer-1 .layer-content').css({ 'opacity': '0.2', 'filter': 'grayscale(100%)' });
		$('#codescrum-hero .overlay .cs-layers-img .layer.layer-3 .layer-content').css({ 'opacity': '0.2', 'filter': 'grayscale(100%)' });
		// Init Typewriter animation
		$('#codescrum-hero .overlay .cs-layers-img .layer.layer-2 .layer-content .layer-moving_dots').addClass("no-width is-animated");
	}, function () {
		// Reset the Styles
		$('#codescrum-hero .overlay .cs-layers-img .layer.layer-1 .layer-content').css({ 'opacity': '1', 'filter': 'grayscale(0%)' });
		$('#codescrum-hero .overlay .cs-layers-img .layer.layer-3 .layer-content').css({ 'opacity': '1', 'filter': 'grayscale(0%)' });
		// Stop Typewriter animation
		$('#codescrum-hero .overlay .cs-layers-img .layer.layer-2 .layer-content .layer-moving_dots').removeClass("no-width is-animated");
	});
	/* ---- Hover Hero Layer 3 --------------------- */
	$('#codescrum-hero .overlay .cs-layers-img .layer.layer-3').hover(function () {
		$('#codescrum-hero .overlay .cs-layers-img .layer.layer-1 .layer-content').css({ 'opacity': '0.2', 'filter': 'grayscale(100%)' });
		$('#codescrum-hero .overlay .cs-layers-img .layer.layer-2 .layer-content').css({ 'opacity': '0.2', 'filter': 'grayscale(100%)' });
		// Init Typewriter animation
		$('#codescrum-hero .overlay .cs-layers-img .layer.layer-3 .layer-content .layer-typewriter').addClass("no-width is-animated");
	}, function () {
		// Reset the Styles
		$('#codescrum-hero .overlay .cs-layers-img .layer.layer-1 .layer-content').css({ 'opacity': '1', 'filter': 'grayscale(0%)' });
		$('#codescrum-hero .overlay .cs-layers-img .layer.layer-2 .layer-content').css({ 'opacity': '1', 'filter': 'grayscale(0%)' });
		// Stop Typewriter animation
		$('#codescrum-hero .overlay .cs-layers-img .layer.layer-3 .layer-content .layer-typewriter').removeClass("no-width is-animated");
	});
}

