/* --------------------------------------------------
	Smooth Scroll
-------------------------------------------------- */

function initSmoothScroll() {

	// Select all links with hashes

	// Remove links that don't actually link to anything
  $('a[href*="#"]').not('[href="#"]').not('[href="#0"]').click(function (event) {
    // On-page links
    if (
      location.pathname.replace(/^\//, '') == this.pathname.replace(/^\//, '')
      &&
      location.hostname == this.hostname
    ) {
      // Figure out element to scroll to
      var target = $(this.hash);
      target = target.length ? target : $('[name=' + this.hash.slice(1) + ']');
      // Does a scroll target exist?
      if (target.length) {
        // Only prevent default if animation is actually gonna happen
        event.preventDefault();
        $('html, body').animate({
          scrollTop: target.offset().top
        }, 1000, function () {

        });
      }
    }
  });
}

/* --------------------------------------------------
  Services Affix
-------------------------------------------------- */

function initServicesAffix() {
  if (window.matchMedia('(min-width: 768px)').matches) {

    console.log("Screen wider than 768px!");

    var serviceAffixLeftSidebarTop = $("#codescrum-services .affix-breakpoint-top").position().top - $(window).height() / 2 + $("#codescrum-services .left-sidebar").height() / 2;
    var serviceAffixMiddleSidebarTop = $("#codescrum-services .affix-breakpoint-top").position().top - $(window).height() / 2 + $("#codescrum-services .middle-sidebar").height() / 2;
    var serviceAffixfooterHeight = $(document).height() - $('.tag-end-serv').position().top + 150;

    $('#codescrum-services .left-sidebar').affix({
		offset: {
			top: serviceAffixLeftSidebarTop,
			bottom: serviceAffixfooterHeight
		}
		}).on('affix.bs.affix', function () { // before affix
			$(this).css({
				/*'top': headerHeight,*/    // for fixed height
        'width': $(this).outerWidth()  // variable widths
			});
		});

		$('#codescrum-services .middle-sidebar').affix({
			offset: {
				top: serviceAffixMiddleSidebarTop,
				bottom: serviceAffixfooterHeight
			}
		}).on('affix.bs.affix', function () { // before affix
			$(this).css({
				/*'top': headerHeight,*/    // for fixed height
        'width': $(this).outerWidth()  // variable widths
			});
		});

		console.log("Affix created!");

		// For multiple navbar's Scrollspy use target empty
    //$('body').scrollspy({target: "", offset: $( window ).height()/2});
    $('body').scrollspy({ target: ".desktopScrollspy", offset: $(window).height() - 50 });

    $("#codescrum-services .desktopScrollspy").on("activate.bs.scrollspy", function () {
      var x = $("#codescrum-services .left-sidebar li.active > a").data("service");
      if (x == "1") {
        $("#codescrum-services").removeClass("bg-color-1 bg-color-2 bg-color-3 bg-color-4").addClass("bg-color-1");
      } else if (x == "2") {
        $("#codescrum-services").removeClass("bg-color-1 bg-color-2 bg-color-3 bg-color-4").addClass("bg-color-2");
      } else if (x == "3") {
        $("#codescrum-services").removeClass("bg-color-1 bg-color-2 bg-color-3 bg-color-4").addClass("bg-color-3");
      } else {
        $("#codescrum-services").removeClass("bg-color-1 bg-color-2 bg-color-3 bg-color-4").addClass("bg-color-4");
      }
      $(".sidebar-section").removeClass("active");
      $("#section" + x).addClass("active");
    });

    console.log("Scrollspy created!");

  } else {
    console.log("Screen smaller than 768px!");

    // var serviceAffixMiddleSidebarTop = $("#codescrum-services .mobile-sidebar").position().top;
    // var serviceAffixfooterHeight = $(document).height() - $('.tag-end-serv').position().top + 150;
    //
    // $('#codescrum-services .mobile-sidebar').affix({
		//   offset: {
    //     top: serviceAffixMiddleSidebarTop,
    //     bottom: serviceAffixfooterHeight
    //   }
    // }).on('affix.bs.affix', function () { // before affix
    //   $(this).css({
    //     //'top': headerHeight,    // for fixed height
    //     'width': $(this).outerWidth()  // variable widths
    //   });
		// });
		//
		// console.log("Affix created!");

		$('body').scrollspy({ target: ".desktopScrollspy", offset: $(window).height() - 200 });

		$("#codescrum-services .desktopScrollspy").on("activate.bs.scrollspy", function () {
			var x = $("#codescrum-services .left-sidebar li.active > a").data("service");
			if (x == "1") {
				$("#codescrum-services").removeClass("bg-color-1 bg-color-2 bg-color-3 bg-color-4").addClass("bg-color-1");
				$(".mobile-sidebar li.active").removeClass("bg-color-1 bg-color-2 bg-color-3 bg-color-4").addClass("bg-color-1");
			} else if (x == "2") {
				$("#codescrum-services").removeClass("bg-color-1 bg-color-2 bg-color-3 bg-color-4").addClass("bg-color-2");
				$(".mobile-sidebar li.active").removeClass("bg-color-1 bg-color-2 bg-color-3 bg-color-4").addClass("bg-color-2");
			} else if (x == "3") {
				$("#codescrum-services").removeClass("bg-color-1 bg-color-2 bg-color-3 bg-color-4").addClass("bg-color-3");
				$(".mobile-sidebar li.active").removeClass("bg-color-1 bg-color-2 bg-color-3 bg-color-4").addClass("bg-color-3");
			} else {
				$("#codescrum-services").removeClass("bg-color-1 bg-color-2 bg-color-3 bg-color-4").addClass("bg-color-4");
				$(".mobile-sidebar li.active").removeClass("bg-color-1 bg-color-2 bg-color-3 bg-color-4").addClass("bg-color-4");
			}
			$(".sidebar-section").removeClass("active");
			$("#section" + x).addClass("active");
		});

		console.log("Scrollspy created!");

	}
}

/* --------------------------------------------------
	Banner Promotion
-------------------------------------------------- */

function initBanner() {

	$(window).scroll(function () {

		if ($(window).scrollTop() > 20) {
			$('#banner_promotion').addClass('banner_promotion_sm', 300);
		}
		else {
			$('#banner_promotion').removeClass('banner_promotion_sm', 300);
		}
	});

	// Dismiss the Banner Promotion on Close icon click
    $('#banner_promotion .close').click(function() {
		$('#banner_promotion').fadeOut();
	})

} // InitBanner

/* --------------------------------------------------
	Navigation | Navbar
-------------------------------------------------- */

function initNavbar() {

	// Sticky Nav & Transparent Background
	$(window).scroll(function () {

		if ($(window).scrollTop() > 20) {
			$('nav').removeClass('navbar-trans', 300);
			$('nav').removeClass('navbar-trans-dark');
			$('nav').addClass('navbar-small', 300);
		}
		else {
			$('nav:not(.mobile-nav)').addClass('navbar-trans', 300);
			$('nav').removeClass('navbar-small', 300);

			if ($('nav').hasClass('trans-helper')) {
				$('nav:not(.mobile-nav)').addClass('navbar-trans-dark');
			}
		}

		$('nav:not(.navbar-fixed-top)').removeClass('navbar-trans navbar-small navbar-trans-dark');

	});


	// Nav on mobile screens
	$(window).resize(function () {
		// Close mobile menu when clicked any side of page
		$(document).on('click', function () {
			$('.collapse').collapse('hide');
		})

		if ($(window).width() <= 1259) {
			$('nav').addClass('mobile-nav');
		} else {
			$('nav').removeClass('mobile-nav');
		}

		if ($('nav').hasClass('mobile-nav')) {
			$('nav').removeClass('navbar-trans');
			$('nav').removeClass('navbar-trans-dark');
		} else {
			if ($(window).width() >= 1259 && $(window).top) {
				$('nav').addClass('navbar-trans');
			}
		}

		// Remove dropdown open on hover for small screens
		if ($('nav').hasClass('mobile-nav')) {

			$('.dropdown-toggle').on('mouseover', function (e) {
				e.preventDefault();

				$('.dropdown').removeClass('open');

				e.stopPropagation();
			});
		}

		// Close mobile menu when clicked hamburger icon
		$("#button_collapse").on('click', function () {
			var x = document.getElementById("collapse").getAttribute("class");
			if (x == "navbar-collapse navbar-right collapse in") {
				$('.collapse').collapse('hide');
				return false;
			}
		})


	}).resize();

	// Bugfix for iOS not scrolling on open menu
	$(".navbar-collapse").css({ maxHeight: $(window).height() - $(".navbar-header").height() + "px" });



	//Reuse navbar links for non home pages. These change from anchors to static pages 
	if (location.pathname != "/" && location.pathname != "/index.html") {

		$("#navbar-home-link").attr("href", "/index.html");
		$("#navbar-main_services-link").attr("href", "/index.html#main-services");
		$("#navbar-main_case-link").attr("href", "/case-studies/");
		$("#navbar-about_us-link").attr("href", "/about-us/");
		$("#navbar-main_commmunity-link").attr("href", "/index.html#main-community");
		$("#navbar-main_contact_us-link").attr("href", "/#main-contact-us");

	}

} // initNavbar

/* --------------------------------------------------
	Video Background
-------------------------------------------------- */

function initVideoBg() {

	var hasBgVideo = $('#fs-video-one-bg').hasClass('player');
	var hasFwBgVideo = $('#fw-video-one-bg').hasClass('player');
	var hasSecBgVideo = $('#section-video').hasClass('player');

	if (hasBgVideo || hasFwBgVideo || hasSecBgVideo) {

		$('.player').YTPlayer();

	}


}

/* --------------------------------------------------
	Portfolio
-------------------------------------------------- */

function initPortfolio() {

	// Filters
	$('.portfolio-filters a').click(function (e) {
		e.preventDefault();

		$('li').removeClass('active');
		$(this).parent().addClass('active');
	});


	// Full Width Gallery (3 columns)
	function pfolio3colFW() {

		var $container = $('#pfolio');
		// init
		$container.isotope({
			// options
			itemSelector: '.portfolio-item',
		});

		// Filter items
		$('#pfolio-filters').on('click', 'a', function () {
			var filterValue = $(this).attr('data-filter');
			$container.isotope({ filter: filterValue });
		});

	} // fwNogap3col


	function pfolioMasonry() {

		var $container = $('.pfolio-items');
		// init
		$container.isotope({
			// options
			itemSelector: '.p-item',
			percentPosition: true,
			layoutMode: 'packery',
			masonry: {
				columnWidth: '.grid-sizer'
			}
		});

		// Filter items
		$('#pfolio-filters').on('click', 'a', function () {
			var filterValue = $(this).attr('data-filter');
			$container.isotope({ filter: filterValue });
		});

	}


	pfolio3colFW();
	pfolioMasonry();

} // initPortfolio

/* --------------------------------------------------
	Parallax
-------------------------------------------------- */


function initParallax() {

	var isSafari = /Safari/.test(navigator.userAgent) && /Apple Computer/.test(navigator.vendor);

	if (!isSafari) {
		$(".main-op").parallax("50%", 0.2);
		$(".number-counters").parallax("50%", 0.2);
		$(".cirlce-counters").parallax("50%", 0.3);
		$(".client-list-parallax").parallax("50%", 0.4);
		$(".ft-slider-parallax").parallax("50%", 0.4);
		$(".testimonials-parallaxx").parallax("50%", 0.4);
		$(".twitter-slider").parallax("50%", 0.4);
		$(".login-2").parallax("50%", 0.2);
	}
}

