$('form#csSimpleForm').validator().on('submit', function (e) {
  if (e.isDefaultPrevented()) {
    // handle the invalid form...
    console.log("Form validation failed!");
  } else {
    // everything looks good!
    console.log('Simple Form Submit Ok');
    $form = $(this);
    $inputs = $form.find("input,select");
    serializeddata = $form.serialize();
    $inputs.prop("disabled", true);
    values = {};
    $inputs.each(function() {
      values[this.name] = $(this).val();
    });
    $('#cs-submit_button').prop('disabled',true);
    $('#cs-submit_button').attr('value','Sending...');
    if(request){
      request.abort();
    }

    request = $.ajax({
      url: '@@GSF@', ///////////////PUT YOUR GOOGLE URL HERE*//////////////////////,
      type: "post",
      data: serializeddata
    });

    request.done(function(response,textStatus, cod){
      $('#cs-submit_button').attr('value','CONTACT US');
      $form.trigger('reset');
      $inputs.prop("disabled", false);
      console.log(response);
      console.log(textStatus);
      console.log(cod);
      window.location.href = '../thanks.html';
    });

    // Callback handler that will be called on failure
    request.fail(function (jqXHR, textStatus, errorThrown){
      // Log the error to the console
      console.error(
        "The following error occurred: "+
        textStatus, errorThrown
      );
      $inputs.prop("disabled", false);
      alert('We could not send the message, try again');
    });

    event.preventDefault();
  }
});
